
import { defineComponent, onMounted, ref, watch } from "vue";
import { CanvasRenderer } from "echarts/renderers";
import * as echarts from "echarts/core";
import VChart from "vue-echarts";
import { use } from "echarts/core";
import { GaugeChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
} from "echarts/components";
import { statusStore } from "@/store/index";

use([
  CanvasRenderer,
  GaugeChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
]);

export default defineComponent({
  components: { VChart },
  props: ["type", "title", "data"],
  setup(props, { attrs }) {
    const oneDOM = ref();
    const secDOM = ref();
    const thirdDOM = ref();
    const tabs = ref();
    const tabs_switch = ref();
    const status = statusStore();
    let doughnut: any;

    const data_index = ref(0);

    onMounted(() => {
      switch (props.type) {
        case "1":
          doughnut = echarts.init(oneDOM.value);
          break;
        case "2":
          doughnut = echarts.init(secDOM.value);
          break;
        case "3":
          doughnut = echarts.init(thirdDOM.value);
          break;
        default:
          break;
      }
      if (status.mode === "dark") {
        doughnut.setOption(darkOption, true);
      } else if (status.mode === "light") {
        doughnut.setOption(option, true);
      }

      window.addEventListener("resize", () => {
        doughnut.resize();
      });

      watch(
        () => status.mode,
        (newMode) => {
          if (newMode === "dark") {
            doughnut.setOption(darkOption, true);
          } else {
            doughnut.setOption(option, true);
          }
        },
        { deep: true }
      );

      watch(
        () => props.data,
        (newData, oldData) => {
          if (newData != oldData) {
            let gaugeData = [
              {
                value: newData[data_index.value].data,
              },
            ];
            let max = newData[data_index.value].max;

            darkOption.series[0].data = gaugeData;
            darkOption.series[0].max = max;
            option.series[0].data = gaugeData;
            option.series[0].max = max;

            if (status.mode === "dark") {
              doughnut.setOption(darkOption, true);
            } else {
              doughnut.setOption(option, true);
            }
          }
        }
      );

      watch(
        () => data_index.value,
        (newIndex, oldIndex) => {
          if (newIndex != oldIndex) {
            let gaugeData = [
              {
                value: props.data[newIndex].data,
              },
            ];
            let max = props.data[newIndex].max;

            darkOption.series[0].data = gaugeData;
            darkOption.series[0].max = max;
            option.series[0].data = gaugeData;
            option.series[0].max = max;

            if (status.mode === "dark") {
              doughnut.setOption(darkOption, true);
            } else {
              doughnut.setOption(option, true);
            }
          }
        }
      );
    });

    const darkOption = {
      // 進度條本身顏色
      color: ["#055FFC"],
      tooltip: {
        trigger: "item",
        backgroundColor: "#383C41",
        borderWidth: 0,
        formatter: "{c}",
        textStyle: {
          color: "#FCFDFE",
        },
        axisPointer: {
          type: "shadow",
        },
      },
      series: [
        {
          type: "gauge",
          startAngle: 90,
          endAngle: -270,
          data: [
            {
              value: props.data[data_index.value].data,
            },
          ],
          // 隱藏指針
          pointer: {
            show: false,
          },
          // 進度條設定
          progress: {
            show: true,
            overlap: false,
            roundCap: true,
            clip: false,
          },
          // 甜甜區底設定
          axisLine: {
            lineStyle: {
              width: 10,
              color: [[1, "#383C41"]],
              show: true,
              roundCap: false,
            },
          },
          // 大刻度
          splitLine: {
            show: false,
            distance: 0,
            length: 10,
          },
          // 小刻度
          axisTick: {
            show: false,
          },
          // 秒數數字
          axisLabel: {
            show: false,
            distance: 50,
          },
          detail: {
            fontSize: 16,
            color: "#F7F9FC",
            formatter: "{value}",
            offsetCenter: ["0%", "5%"],
          },
          min: 0,
          max: props.data[data_index.value].max,
        },
      ],
    };

    const option = {
      // 進度條本身顏色
      color: ["#055FFC"],
      tooltip: {
        trigger: "item",
        backgroundColor: "#383C41",
        borderWidth: 0,
        formatter: "{c}",
        textStyle: {
          color: "#FCFDFE",
        },
        axisPointer: {
          type: "shadow",
        },
      },
      series: [
        {
          type: "gauge",
          startAngle: 90,
          endAngle: -270,
          data: [
            {
              value: props.data[data_index.value].data,
            },
          ],
          // 隱藏指針
          pointer: {
            show: false,
          },
          // 進度條設定
          progress: {
            show: true,
            overlap: false,
            roundCap: true,
            clip: false,
          },
          // 甜甜區底設定
          axisLine: {
            lineStyle: {
              width: 10,
              color: [[1, "#D8DEE7"]],
              show: true,
              roundCap: false,
            },
          },
          // 大刻度
          splitLine: {
            show: false,
            distance: 0,
            length: 10,
          },
          // 小刻度
          axisTick: {
            show: false,
          },
          // 秒數數字
          axisLabel: {
            show: false,
            distance: 50,
          },
          detail: {
            fontSize: 16,
            color: "#383C41",
            formatter: "{value}",
            offsetCenter: ["0%", "5%"],
          },
          min: 0,
          max: props.data[data_index.value].max,
        },
      ],
    };

    function clickTab(status: string, event: any, index: number): void {
      if (status === "tabs") {
        for (let item of tabs.value.children) {
          item.className = "false";
        }
      } else {
        for (let item of tabs_switch.value.children) {
          item.className = "false";
        }
        data_index.value = index;
      }
      event.path[0].className = "true";
    }

    return {
      option,
      tabs_switch,
      tabs,
      clickTab,
      oneDOM,
      secDOM,
      thirdDOM,
      data_index,
    };
  },
});
